.App{
    width: 80%;
    margin: 0px auto;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.navbar{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 100px;
    color: rgba(202, 31, 103, 0.973) ;
    border-bottom: 1px solid #f5eeee;
}
.home{
    font-size: small;
    color:rgb(134, 146, 146)
}
.navbar h1{
    flex-grow: 1;
}
.links a {
    margin-left: 20px;
    text-decoration: none;
    padding: 10px 4px;
    font-family: Tahoma, sans-serif ;
}
a:hover{
    color: rgb(132, 153, 223);
    background-color: aliceblue;
    padding: 10px 4px;
    border-radius: 5px;
}
.blog-details h1{
    color: #f1356d;
}
.blog-details button{
    background-color: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 20px;
    cursor: pointer;
    margin-top: 15px;
}
.blog-preview{
    padding: 5px 20px;
    border-bottom: 1px solid #fafafa
}
.blog-preview:hover{
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.blog-preview h1{
    color:rgba(245, 16, 111, 0.973) ;
}
.blog-preview a{
    text-decoration: none;
}
.create{
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}
.create label{
    text-align: left;
    display: block;
}
.create input, .create textarea, .create select{
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}
.create button{
    background-color: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 20px;
    cursor: pointer;
}
.create button:active{
    background-color: #3d0d1b;
    color: #fff;
    border-radius: 20px;
    cursor:default;
}
.not-found h1{
    color:#f1356d;
}
.not-found a{
    text-decoration: none;
}